* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
a {
  text-decoration: none;
  color: #3b4cca;
}
.main-box {
  width: 990px;
  margin: 0 auto;
  padding: 10px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}
.sources {
  width: 100%;
  color: #9e9e9e;
  font-size: 8px;
  margin-top: 2px;
  text-align: right;
}
.main-box > a:first-child {
  width: 100%;
}
.main-box > a:first-child > div {
  width: 100%;
  padding: 0 20px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}
.logo-div > img:first-child {
  width: 100px;
  margin-right: 10px;
}
.logo-div > div {
  width: 300px;
  height: 150px;
  overflow: hidden;
}
.logo-div > div > img {
  width: 100%;
  object-fit: cover;
  object-position: 0 -15px;
}
.filter-box {
  width: 25%;
  padding: 0 10px 0 0;
}
.filter-box > h2 {
  text-transform: uppercase;
  text-align: center;
  font-size: 18px;
  font-weight: 700;
  color: white;
  background-color: #cc0000;
  padding: 10px;
  margin: 0 0 5px 0;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}
.typelink {
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 2px;
  font-size: 12px;
  font-weight: 700;
  background-color: #eeeeee;
  padding: 6px;
}
.filter-box > a:last-child,
.filter-box > a:last-child > div {
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  overflow: hidden;
}
.filter-box div:hover {
  background-color: #f5fab5;
}
.filter-box div:active {
  background-color: #696969;
  color: white;
}
.display-box {
  width: 75%;
}
.filter-display {
  width: 100%;
  display: flex;
  flex-flow: row wrap;
}
.filter-sub-display {
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  background-color: #f0f0f0;
  padding: 4px 3px;
  border-bottom-right-radius: 15px;
  border-bottom-left-radius: 15px;
}
.filter-sub-display > a:hover {
  background-color: #d8d8fa;
}
.filter-display > h3 {
  width: 100%;
  background-color: #3b4cca;
  color: white;
  padding: 10px;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 5px;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}
.pokemon-cont {
  width: 170px;
  height: 180px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: space-between;
  padding: 20px;
  margin: 5px 5px;
  background-color: white;
  border-radius: 10px;
}
.pokemon-cont > div:first-child {
  width: 120px;
  overflow: hidden;
}
.pokemon-cont img {
  width: 100%;
  height: 100px;
  object-fit: contain;
}
.pokemon-cont p {
  width: 100%;
  text-align: center;
  padding: 6px;
  margin-top: 5px;
  font-size: 13px;
  font-weight: 500;
  background-color: #d9ddff;
  color: black;
  border-radius: 10px;
}
.pokemon-details {
  margin: 0 30px;
  display: flex;
  flex-flow: row wrap;
  background-color: white;
}
.pokemon-details > div:first-child {
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  border-top: 5px solid #dfdfe2;
  border-left: 5px solid #dfdfe2;
  border-bottom: 5px solid #dfdfe2;
  background-color: #f1f1ff;
  width: 40%;
  overflow: hidden;
  padding: 25px;
}
.pokemon-details img {
  width: 100%;
  object-fit: contain;
}
.pokemon-details > div:last-child {
  width: 60%;
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;
  border-top: 5px solid #dfdfe2;
  border-right: 5px solid #dfdfe2;
  border-bottom: 5px solid #dfdfe2;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
}
.pokemon-details > div:last-child > div:first-child {
  position: relative;
  display: flex;
  flex-wrap: wrap;
}
.pokemon-details > div:last-child > div:first-child > div:first-child {
  width: 50px;
  height: 50px;
  padding-top: 11px;
  font-size: 20px;
  font-weight: 700;
  color: white;
  background-color: #cc0000;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  position: absolute;
}
.pokemon-details > div:last-child > div:first-child > div:nth-child(2) {
  text-transform: uppercase;
  font-size: 24px;
  font-weight: 700;
  width: 90%;
  margin-left: 30px;
  margin-top: 5px;
  padding: 5px 3px 3px 30px;
  border-radius: 10px;
  background-color: #d9ddff;
  color: #3b4cca;
}
.pokemon-details > div:last-child > div:first-child > div:nth-child(3) {
  font-size: 14px;
  margin-top: 10px;
  padding: 10px;
}
.typebox {
  width: 80px;
  margin: 5px;
  padding: 5px;
  font-size: 12px;
  font-weight: 500;
  text-align: center;
  text-transform: uppercase;
  border-radius: 10px;
  color: white;
}
.typebox:hover {
  background-color: #ffb84e;
  color: black;
}
.typebox:active {
  background-color: #ff9f0f;
  color: white;
}
.Normal,
.filter-display > .Normal {
  background-color: #f5fab5;
  color: black;
}
.Fire,
.filter-display > .Fire {
  background-color: #e41f1f;
}
.Water,
.filter-display > .Water {
  background-color: #515dbb;
}
.Electric,
.filter-display > .Electric {
  background-color: #f4fd71;
  color: black;
}
.Grass,
.filter-display > .Grass {
  background-color: #12b81b;
}
.Ice,
.filter-display > .Ice {
  background-color: #1c99ca;
}
.Fighting,
.filter-display > .Fighting {
  background-color: #bb7b1a;
}
.Poison,
.filter-display > .Poison {
  background-color: #8d1abb;
}
.Ground,
.filter-display > .Ground {
  background-color: #855000;
}
.Flying,
.filter-display > .Flying {
  background-color: #c1edff;
  color: black;
}
.Bug,
.filter-display > .Bug {
  background-color: #a6faaa;
  color: black;
}
.Rock,
.filter-display > .Rock {
  background-color: #7e7d7d;
}
.Steel,
.filter-display > .Steel {
  background-color: #2c2c2c;
}
.pokemon-details > div:last-child > div:last-child {
  width: 100%;
  text-align: right;
}
.pokemon-details button {
  padding: 5px 20px;
  background-color: #f5ec99;
  border: 2px solid #7b85c4;
  border-radius: 10px;
  font-size: 13px;
  text-transform: uppercase;
  font-weight: 500;
}
.pokemon-details button:hover {
  border: 3px solid #ff9f0f;
  background-color: #ff9f0f;
  color: white;
}
